// page-button

.page-button {
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 100px;
  max-width: 150px;
  min-height: 32px;
  padding-top: 5px;
  padding-right: 10px;
  padding-bottom: 5px;
  padding-left: 10px;
  color: currentColor;
  font-size: 14px;
  line-height: 1.5;
  font-weight: 700;
  text-decoration: none;
  border: 1px solid @page-gray;
  border-radius: 25px;
  transition-property: background-color, color, opacity;
  transition-duration: 0.2s;
  transition-timing-function: linear;

  &:hover {
    color: @basic-white;
    background-color: @page-green;
  }

  &:active {
    opacity: 0.5;
  }

  @media (min-width: @s) {
    min-height: 50px;
    font-size: 16px;
  }
}
