// latest-devs

.latest-devs__content {
  padding-top: 36px;
  padding-bottom: 77px;

  @media (min-width: @m) {
    width: @m;
    .centered();
  }

  @media (min-width: @l) {
    width: @l;
  }
}

.latest-devs__title {
  margin-bottom: 32px;

  @media (min-width: @s) {
    margin-bottom: 43;
    text-align: center;
  }

  @media (min-width: @l) {
    margin-bottom: 43px;
  }
}

.latest-devs__form {
  margin-bottom: 35px;

  @media (min-width: @s) {
    margin-bottom: 45px;
  }
}

.latest-devs__houses {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  .mp-reset();
  margin-bottom: 40px;
  list-style: none;

  @media (min-width: @xs-600) {
    flex-direction: row;
    justify-content: space-between;
    align-items: stretch;
  }

  @media (min-width: @s) {
    margin-bottom: 60px;
  }

  @media (min-width: @m) {
    justify-content: flex-start;
  }
}

.latest-devs__card {
  min-height: 292px;
  margin-bottom: 38px;

  @media (min-width: @xs-600) {
    width: 48.8%;
  }

  @media (min-width: @m) {
    width: 32.3%;
    margin-right: 13px;

    &:nth-child(3n) {
      margin-right: 0;
    }
  }

  @media (min-width: @l) {
    max-width: 379px;
    margin-right: 21px;
  }
}

.latest-devs__card--hidden {
  display: none;
}

.latest-devs__more-btn {
  margin: 0 auto;

  &:hover {
    .latest-devs__more-text::before,
    .latest-devs__more-text::after {
      background-color: @basic-white;
    }
  }
}

.latest-devs__more-text {
  position: relative;

  &::before,
  &::after {
    content: "";
    position: absolute;
    width: 1px;
    height: 8px;
    border-radius: 2px;
    background-color: @basic-black;
  }

  &::before {
    top: 4px;
    right: -9px;
    transform: rotate(-30deg);
  }

  &::after {
    bottom: 2px;
    right: -9px;
    transform: rotate(25deg);
  }

  @media (min-width: @s) {
    &::before {
      top: 5px;
    }

    &::after {
      bottom: 4px;
    }
  }
}
