// house-card

.house-card {
  position: relative;
  background-color: @page-ultra-light;
  border: 1px solid @page-light-gray;
  transition-property: opacity, transform;
  transition-duration: 0.2s;
  transition-timing-function: linear;

  &:hover {
    opacity: 0.8;
    transform: translateY(-5px);
  }

  &:active {
    opacity: 0.5;
    transform: translateY(5px);
  }
}

.house-card__img-link {
  display: block;

  &::before {
    content: "";
    position: absolute;
    opacity: 0.2;
    width: 100%;
    height: 100%;
  }
}

.house-card__img {
  width: 100%;
}

.house-card__label {
  position: absolute;
  top: 50%;
  right: 0;
  min-width: 134px;
  padding-top: 8px;
  padding-right: 11px;
  padding-bottom: 5px;
  padding-left: 10px;
  color: @basic-white;
  font-size: 10px;
  line-height: 1.33;
  font-weight: 700;
  text-align: center;
  background-color: @page-green;

  @media (min-width: @xs-600) {
    top: 46%;
  }

  @media (min-width: @s) {
    top: 51%;
    font-size: 12px;
  }

  @media (min-width: @m) {
    top: 138px;
  }

  @media (min-width: @l) {
    top: 198px;
  }
}

.house-card__lable--ind {
  background-color: @page-turq;
}

.house-card__lable--sup {
  background-color: @page-orange;
}

.house-card__desc {
  padding: 15px;

  @media (min-width: @s) {
    padding: 20px;
  }
}

.house-card__address {
  margin-bottom: 12px;
  font-style: normal;

  @media (min-width: @s) {
    margin-bottom: 15px;
  }
}

.house-card__sale-info {
  margin-bottom: 3px;

  @media (min-width: @s) {
    margin-bottom: 7px;
  }
}

.house-card__price {
  word-break: break-all;
}

.house-card__avail {
  font-size: 12px;

  @media (min-width: @s) {
    font-size: 14px;
  }
}
