// filter-form

.filter-form {
  width: 100%;
}

.filter-form__content {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  max-width: 474px;
}

.filter-form__label {
  margin-right: 8px;
  font-size: 14px;
  line-height: 1.71;
  font-weight: 700;

  @media (min-width: @s) {
    font-size: 16px;
  }
}

.filter-form__input {
  flex-grow: 1;
  max-width: 418px;
  min-height: 32px;
  padding-top: 7px;
  padding-right: 10px;
  padding-bottom: 7px;
  padding-left: 10px;
  border: 1px solid @page-light-gray;
  border-radius: 25px;
  transition-property: border-color;
  transition-duration: 0.2s;
  transition-timing-function: linear;

  &:hover {
    border-color: @page-green;
  }

  @media (min-width: @s) {
    min-height: 50px;
  }

  @media (min-width: @l) {
    padding-right: 20px;
    padding-left: 20px;
  }
}
